body {
    font-family: "lato";
  }
  body {
    background: hsl(329, 87%, 59%);
    display: flex;
    min-height: 100vh;
    justify-content: center;
    align-items: center;
  }
  
  .form-container {
    width: 360px;
    background-color: rgb(245, 147, 239);
    margin: auto;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
    padding: 10px;
  }
  
  .register-form {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 10px;
  }
  
  .success-message {
    font-family: "Roboto", sans-serif;
    background-color: #3ff886;
    padding: 15px;
    color: rgb(255, 255, 255);
    text-align: center;
  }
  
  .form-field {
    margin: 10px 0 10px 0;
    padding: 15px;
    font-size: 16px;
    border: 0;
    font-family: "Roboto", sans-serif;
  }
  
  span {
    font-family: "Roboto", sans-serif;
    font-size: 14px;
    color: red;
    margin-bottom: 15px;
  }
  
  input {
    background: #f2f2f2;
  }
  
  .error {
    border-style: solid;
    border: 2px solid #77ceca;
  }
  
  button {
    background: #54bcc0;
    color: rgb(133, 22, 59);
    cursor: pointer;
  }
  
  button:disabled {
    cursor: default;
  }